<script>
import sectionTitle from "@/components/section-title";
import TronService from "@/base/TronService";
import { getTime, getTimeInterval } from "@/utils";
// import Quote from "@/components/quote";
// import {fetchFriend} from '../api'
export default {
  name: "index",
  data() {
    return {
      dialogVisible: false,
      dialogVisible2: false,
      list: [],
      walletAddress: null,
      requestAccountsResponse: {
        code: 0, // 200：ok，4000：在队列中，不需要重复提交， 4001：user rejected
        message: "",
      },

      ml: "",
      md: "",
      sl: "",
      sd: "",
      balance1: "",
      balance2: "",
    };
  },
  components: {
    // Quote,
    sectionTitle,
  },
  methods: {
    async linkWallet() {
      var obj = setInterval(async () => {
        if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
          clearInterval(obj);
          console.log(this.$tronLink);
          
          this.walletAddress = this.$tronWeb.defaultAddress.base58;
          console.log(this.$tronWeb.defaultAddress.base58);
          this.$sunWeb.mainchain.trx
            .getBandwidth(this.walletAddress)
            .then((r) => {
              console.log("主网带宽剩余：" + r);
              this.md = "主网带宽剩余：" + r;
            });
          this.$sunWeb.sidechain.trx
            .getBandwidth(this.walletAddress)
            .then((r) => {
              console.log("侧链带宽剩余：" + r);
              this.sd = "侧链带宽剩余：" + r;
            });

          //查询主网资源
          this.$sunWeb.mainchain.trx
            .getAccountResources(this.walletAddress)
            .then((tr) => {
              let rr = { 地址: this.walletAddress };
              let tre = tr.EnergyUsed > 0 ? tr.EnergyUsed : 0;
              let trr = tr.EnergyLimit - tre;
              console.log("主网能量剩余：" + trr);
              rr["主网能量剩余"] = trr;

              //查询侧链资源
              this.$sunWeb.sidechain.trx
                .getAccountResources(this.walletAddress)
                .then((sr) => {
                  let sre = sr.EnergyUsed > 0 ? sr.EnergyUsed : 0;
                  let srr = sr.EnergyLimit - sre;
                  console.log("侧链能量剩余：" + srr);
                  rr["侧链能量剩余"] = srr;
                  console.log(rr);

                  this.ml = "主网能量剩余：" + trr;
                  this.sl = "侧链能量剩余：" + srr;
                });
            });
        }
      }, 10);
    },
    //查询钱包余额
    getBalance() {
      var obj = setInterval(async () => {
        if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
          clearInterval(obj);
          //当前连接的钱包地址获取 window.tronWeb.defaultAddress.base58
          let balance1 = await this.$tronWeb.trx.getBalance(this.walletAddress);
          let balance2 = await this.$sunWeb.sidechain.trx.getBalance(
            this.walletAddress
          );
          this.balance1 = "主网balance：" + balance1;
          this.balance2 = "侧链balance：" + balance2;
          console.log("主网balance：", balance1);
          console.log("侧链balance：", balance2);
        }
      }, 10);
    },
    getinfo() {
      this.$alert("tronlink钱包主网和侧链信息", "链上信息", {
        confirmButtonText: "确定",
        callback: (action) => {
          this.$message({
            type: "info",
            message: "地址：" + this.walletAddress + "",
          });
        },
      });
    },
    getTronweb() {
      var obj = setInterval(async () => {
        if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
          clearInterval(obj);
          document.write(
            "Yes, catch it:",
            window.tronWeb.defaultAddress.base58
          );
        }
      }, 10);
    },

    open() {
      this.$alert("没有任何消息", "糟糕！！！", {
        confirmButtonText: "确定",
        callback: (action) => {
          this.$message({
            type: "info",
            message: "糟糕！！！",
          });
        },
      });
    },
    deploy_contract() {
      // new TronService().bushu()
    },

    // triggercontract(){
    // 	new TronService().zhixing()
    // },

    // call(){
    // 	new TronService().chaxun()
    // },

    handleClose(done) {
      this.$confirm("确认关闭？")
        .then((_) => {
          done();
        })
        .catch((_) => {});
    },
  },
  mounted() {
    //   this.getTronweb()
    this.linkWallet();
    this.getBalance();
  },
};
