var TronBase = require("@/base/TronBase");

class TronService{
	constructor() {
	}
	
	//测试查询波场资源
	resources(fn,address){
		TronBase.init(tronBase=>{
			console.log(tronBase.title);
			
			let addresstest='TTT9kQmmvWXg9jZwa4cDAPdBUAPugU2L2G';
			let addre = address==null?addresstest:address;
			
			console.log(tronBase.title)
			tronBase.sunWeb.mainchain.trx.getBandwidth(addre).then(r=>console.log("主网带宽剩余："+r))
			tronBase.sunWeb.sidechain.trx.getBandwidth(addre).then(r=>console.log("侧链带宽剩余："+r))
			
			//查询主网资源
			tronBase.sunWeb.mainchain.trx.getAccountResources(addre).then(tr=>{
				let rr={"地址":addre};
				let tre= tr.EnergyUsed>0?tr.EnergyUsed:0;
				let trr= tr.EnergyLimit-tre;
				console.log("主网能量剩余："+trr);
				rr["主网能量剩余"]=trr;
				
				//查询侧链资源
				tronBase.sunWeb.sidechain.trx.getAccountResources(addre).then(sr=>{
					let sre=sr.EnergyUsed>0?sr.EnergyUsed:0;
					let srr= sr.EnergyLimit-sre;
					console.log("侧链能量剩余："+srr)
					rr["侧链能量剩余"]=srr;
					console.log(rr);
					fn(rr);
				})
			})
			
		});
		
	}
	
	//token转账
	transfer(fn,num,to,tokenId){
		TronBase.init(tronBase=>{
			let t=to ==null?'TTT9kQmmvWXg9jZwa4cDAPdBUAPugU2L2G':to;
			let id= tokenId==null?'1002417':tokenId;
			tronBase.sunWeb.sidechain.trx.sendToken(t,num*tronBase.tokenAccuracy,id,tronBase.privateKey).then(r=>{
				if(r.result==true){
					console.log("Borcoin 成功转账："+num+" 个");
					fn({"转向地址":t,"数量":num,"转账txid":r.txid,"消息":"Borcoin 成功转账"});
				}else{
					fn({"转向地址":t,"数量":num,"消息":"Borcoin 转账失败"});
				}
				
			});
		});
	}
	
	//
	bushu(){
		alert("确定")
		TronBase.init(tronBase=>{
			this.deploy_contract(tronBase);
			
			
		});
		
		
	}
	
	
	zhixing(){
		alert("确定")
		TronBase.init(tronBase=>{
			this.triggercontract(tronBase);
			
		});
		
	}
	
	chaxun(){
		TronBase.init(tronBase=>{
			this.triggercontractCall(tronBase);
			
		});
		
	}
	
	
	//先在 http://www.tronide.io/ 上部署测试网，根据NetWork请求复制参数填入，然后复制请求中的txid至区块浏览器查询
	deploy_contract(tronBase){
		let abi = "[{\"constant\":true,\"inputs\":[],\"name\":\"name\",\"outputs\":[{\"name\":\"\",\"type\":\"string\"}],\"payable\":false,\"stateMutability\":\"view\",\"type\":\"function\"},{\"constant\":false,\"inputs\":[{\"name\":\"_spender\",\"type\":\"address\"},{\"name\":\"_value\",\"type\":\"uint256\"}],\"name\":\"approve\",\"outputs\":[{\"name\":\"success\",\"type\":\"bool\"}],\"payable\":false,\"stateMutability\":\"nonpayable\",\"type\":\"function\"},{\"constant\":true,\"inputs\":[],\"name\":\"totalSupply\",\"outputs\":[{\"name\":\"\",\"type\":\"uint256\"}],\"payable\":false,\"stateMutability\":\"view\",\"type\":\"function\"},{\"constant\":false,\"inputs\":[{\"name\":\"_from\",\"type\":\"address\"},{\"name\":\"_to\",\"type\":\"address\"},{\"name\":\"_value\",\"type\":\"uint256\"}],\"name\":\"transferFrom\",\"outputs\":[{\"name\":\"success\",\"type\":\"bool\"}],\"payable\":false,\"stateMutability\":\"nonpayable\",\"type\":\"function\"},{\"constant\":true,\"inputs\":[],\"name\":\"decimals\",\"outputs\":[{\"name\":\"\",\"type\":\"uint8\"}],\"payable\":false,\"stateMutability\":\"view\",\"type\":\"function\"},{\"constant\":false,\"inputs\":[{\"name\":\"_value\",\"type\":\"uint256\"}],\"name\":\"burn\",\"outputs\":[{\"name\":\"success\",\"type\":\"bool\"}],\"payable\":false,\"stateMutability\":\"nonpayable\",\"type\":\"function\"},{\"constant\":false,\"inputs\":[{\"name\":\"_value\",\"type\":\"uint256\"}],\"name\":\"unfreeze\",\"outputs\":[{\"name\":\"success\",\"type\":\"bool\"}],\"payable\":false,\"stateMutability\":\"nonpayable\",\"type\":\"function\"},{\"constant\":true,\"inputs\":[{\"name\":\"\",\"type\":\"address\"}],\"name\":\"balanceOf\",\"outputs\":[{\"name\":\"\",\"type\":\"uint256\"}],\"payable\":false,\"stateMutability\":\"view\",\"type\":\"function\"},{\"constant\":false,\"inputs\":[{\"name\":\"newOwer\",\"type\":\"address\"}],\"name\":\"transferOwnerShip\",\"outputs\":[],\"payable\":false,\"stateMutability\":\"nonpayable\",\"type\":\"function\"},{\"constant\":true,\"inputs\":[],\"name\":\"owner\",\"outputs\":[{\"name\":\"\",\"type\":\"address\"}],\"payable\":false,\"stateMutability\":\"view\",\"type\":\"function\"},{\"constant\":true,\"inputs\":[],\"name\":\"symbol\",\"outputs\":[{\"name\":\"\",\"type\":\"string\"}],\"payable\":false,\"stateMutability\":\"view\",\"type\":\"function\"},{\"constant\":false,\"inputs\":[{\"name\":\"_to\",\"type\":\"address\"},{\"name\":\"_value\",\"type\":\"uint256\"}],\"name\":\"transfer\",\"outputs\":[{\"name\":\"success\",\"type\":\"bool\"}],\"payable\":false,\"stateMutability\":\"nonpayable\",\"type\":\"function\"},{\"constant\":false,\"inputs\":[{\"name\":\"_target\",\"type\":\"address\"},{\"name\":\"_amount\",\"type\":\"uint256\"}],\"name\":\"addCoin\",\"outputs\":[],\"payable\":false,\"stateMutability\":\"nonpayable\",\"type\":\"function\"},{\"constant\":true,\"inputs\":[{\"name\":\"\",\"type\":\"address\"}],\"name\":\"frozenAccount\",\"outputs\":[{\"name\":\"\",\"type\":\"bool\"}],\"payable\":false,\"stateMutability\":\"view\",\"type\":\"function\"},{\"constant\":true,\"inputs\":[{\"name\":\"\",\"type\":\"address\"}],\"name\":\"freezeOf\",\"outputs\":[{\"name\":\"\",\"type\":\"uint256\"}],\"payable\":false,\"stateMutability\":\"view\",\"type\":\"function\"},{\"constant\":false,\"inputs\":[{\"name\":\"_value\",\"type\":\"uint256\"}],\"name\":\"freeze\",\"outputs\":[{\"name\":\"success\",\"type\":\"bool\"}],\"payable\":false,\"stateMutability\":\"nonpayable\",\"type\":\"function\"},{\"constant\":true,\"inputs\":[{\"name\":\"_owner\",\"type\":\"address\"},{\"name\":\"_spender\",\"type\":\"address\"}],\"name\":\"allowance\",\"outputs\":[{\"name\":\"remaining\",\"type\":\"uint256\"}],\"payable\":false,\"stateMutability\":\"view\",\"type\":\"function\"},{\"constant\":false,\"inputs\":[{\"name\":\"_target\",\"type\":\"address\"},{\"name\":\"_freeze\",\"type\":\"bool\"}],\"name\":\"freezeAccount\",\"outputs\":[],\"payable\":false,\"stateMutability\":\"nonpayable\",\"type\":\"function\"},{\"inputs\":[{\"name\":\"initialSupply\",\"type\":\"uint256\"},{\"name\":\"tokenName\",\"type\":\"string\"},{\"name\":\"decimalUnits\",\"type\":\"uint8\"},{\"name\":\"tokenSymbol\",\"type\":\"string\"}],\"payable\":false,\"stateMutability\":\"nonpayable\",\"type\":\"constructor\"},{\"anonymous\":false,\"inputs\":[{\"indexed\":true,\"name\":\"from\",\"type\":\"address\"},{\"indexed\":true,\"name\":\"to\",\"type\":\"address\"},{\"indexed\":false,\"name\":\"value\",\"type\":\"uint256\"}],\"name\":\"Transfer\",\"type\":\"event\"},{\"anonymous\":false,\"inputs\":[{\"indexed\":true,\"name\":\"from\",\"type\":\"address\"},{\"indexed\":false,\"name\":\"value\",\"type\":\"uint256\"}],\"name\":\"Burn\",\"type\":\"event\"},{\"anonymous\":false,\"inputs\":[{\"indexed\":true,\"name\":\"from\",\"type\":\"address\"},{\"indexed\":false,\"name\":\"value\",\"type\":\"uint256\"}],\"name\":\"Freeze\",\"type\":\"event\"},{\"anonymous\":false,\"inputs\":[{\"indexed\":true,\"name\":\"from\",\"type\":\"address\"},{\"indexed\":false,\"name\":\"value\",\"type\":\"uint256\"}],\"name\":\"Unfreeze\",\"type\":\"event\"},{\"anonymous\":false,\"inputs\":[{\"indexed\":true,\"name\":\"from\",\"type\":\"address\"},{\"indexed\":false,\"name\":\"amount\",\"type\":\"uint256\"}],\"name\":\"AddSupply\",\"type\":\"event\"},{\"anonymous\":false,\"inputs\":[{\"indexed\":true,\"name\":\"from\",\"type\":\"address\"},{\"indexed\":false,\"name\":\"frozen\",\"type\":\"bool\"}],\"name\":\"FrozenFunds\",\"type\":\"event\"},{\"anonymous\":false,\"inputs\":[{\"indexed\":true,\"name\":\"_owner\",\"type\":\"address\"},{\"indexed\":true,\"name\":\"_spender\",\"type\":\"address\"},{\"indexed\":false,\"name\":\"_value\",\"type\":\"uint256\"}],\"name\":\"Approval\",\"type\":\"event\"}]";
		let code = "";
	    tronBase.sunWeb.sidechain.contract().new({
			abi:JSON.parse(abi),
			bytecode:code,
			feeLimit:100000000,
			callValue:0,
			userFeePercentage:10,
			originEnergyLimit:1000000,
			parameters:[100000000,"RoamSideCoin",8,"ROAM-S"]
		  }).then(r=>{
			  console.log(r);
			  
			  
		  });
	    // console.log(contract_instance.address);
	}
	
	//部署智能合约
	async triggercontract(tronBase){
	    try {
	        let instance = await tronBase.sunWeb.sidechain.contract().at('TW4RDDafW1y7GRrjE3pVwGHtx8MTJ8UTu5');
	        let res = await instance.addCoin('TU3KHYr2BAivinMHQQ4AQEVF3N1oAe6bZG',1000000000000).send({
	            feeLimit:100000000,
	            callValue:0,
	            shouldPollResponse:true
	        });
	
	        console.log(res);
	
	    } catch (error) {
	        console.log(error);
	    }
	}
	
	//
	async triggercontractCall(tronBase){
	   let instance = await tronBase.sunWeb.sidechain.contract().at('TW4RDDafW1y7GRrjE3pVwGHtx8MTJ8UTu5');
	   let res = await instance.totalSupply().call();
	   console.log(res);
	   alert(res)
	}
	
}

module.exports = TronService;